import React, { Component } from 'react';

class Resume extends Component {
  render() {

    if(this.props.data){
      var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map(function(education){
        return <div key={education.school}><h3>{education.school}</h3>
        <p className="info">{education.degree} <span>&bull;</span><em className="date">{education.graduated}</em></p>
        <p>{education.description}</p></div>
      })
      var certification = this.props.data.certification.map(function(certification){
        return <div key={certification.company}><h3>{certification.company}</h3>
        <p className="info">{certification.cert} </p>
        <p>{certification.date}<br />{certification.validation}</p></div>
      })
      var badge = this.props.data.badge.map(function(badge){
         var badgeImage = 'images/'+badge.badge;
         return <div key={badge.badge}>
         <div className="item-wrap">
            <a href={badge.verify}>
               <img alt={badge} src={badgeImage} />
               </a></div></div>
      })
      var skills = this.props.data.skills.map(function(skills){
        var className = 'bar-expand '+skills.name.toLowerCase();
        return <li key={skills.name}><span style={{width:skills.level}}className={className}></span><em>{skills.name}</em></li>
      })
    }

    return (
      <section id="resume">

      <div className="row education">
         <div className="three columns header-col">
            <h1><span>Education</span></h1>
         </div>

         <div className="nine columns main-col">
            <div className="row item">
               <div className="twelve columns">
                 {education}
               </div>
            </div>
         </div>
      </div>

      <div className="row certification">
         <div className="three columns header-col">
            <h1><span>Certification</span></h1>
         </div>
         
         <div className="row badges">
            <div className="six columns">
               {badge}
            </div>
         </div>
         <div className="three rows verification">
            <p><span>* Click to verify</span></p>
         </div>
      </div>

      <div className="row skill">

         <div className="three columns header-col">
            <h1><span>Skills</span></h1>
         </div>

         <div className="nine columns main-col">

            <p>{skillmessage}
            </p>

				<div className="bars">
				   <ul className="skills">
					  {skills}
					</ul>
				</div>
			</div>
      </div>
   </section>
    );
  }

   newMethod() {
      return 'images/' + this.props.data.image;
   }
}

export default Resume;
